.markup {
  .row {
	display: flex;
	flex-wrap: wrap;
  }

  .col-lg-4.col-md-6.col-xs-12 {
	width: 100%;
	padding: 25px;
	box-shadow: 0 0 1px rgba(0, 0, 0, 0.27);

	@include media('>=tablet') {
	  width: 50%;
	}

	@include media('>=1280px') {
	  width: calc(100% / 3);
	}

	&:hover {
	  box-shadow: 0 0 1px rgb(192, 17, 17);
	}
  }

  .scroll {
	max-height: 350px;
  }

  .site-map {
	ol {
	  padding-left: 40px;
	  padding-bottom: 20px;

	  li {
		position: relative;
		border-left: 2px solid $primary-color;

		&:hover {
		  cursor: pointer;

		  &>a {
			text-decoration: underline;
		  }

		  &:before {
			background-color: $secondary-color;
		  }
		}

		a {
		  line-height: 40px;
		  margin-left: 15px;
		  display: flex;
		}

		&:before {
		  content: "";
		  position: absolute;
		  left: -26px;
		  top: 15px;
		  width: 10px;
		  height: 10px;
		  background-color: $primary-color;
		  border-radius: 50%;

		}

		&:after {
		  content: "";
		  position: absolute;
		  left: -2px;
		  top: 25px;
		  width: 50%;
		  height: 15px;
		  border-left: 2px solid $primary-color;
		  border-bottom: 2px solid $primary-color;
		}
	  }
	}
  }

  .gradient-default {
	background: radial-gradient(rgb(206, 196, 196), transparent);
	transition: all 2s;

	&:hover {
	  background: radial-gradient(red, transparent);
	}
  }
}

body {
  color: $color;
  font-size: $font-size;
  line-height: $line-height;
  font-family: $font-primary;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding: 0;
  margin: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.wrap_page {
  overflow: hidden;
  margin-bottom: auto;
}

.scrolltop {
  display: none;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  bottom: 20px;
  right: 10px;
  z-index: 20;
}

.muve-top {
  position: absolute;
  right: 20px;
  bottom: 20px;
  padding: 20px;
  background: $muve-top-background;
  color: $muve-top-color;
  cursor: pointer;
  transition: 0.5s;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);

  &:before {
	content: "↑";
	position: absolute;
	font-weight: bold;
	font-size: 20px;
	top: 47%;
	left: 52%;
	transform: translate(-50%, -50%);
  }
}

/*1*/
[class*='brand-bg'] {
  background-color: $primary-color;
  height: 15vh;
  width: 75%;
  margin-bottom: 25px;
}

.brand-bg-2 {
  background-color: $secondary-color;
}

.brand-bg-3 {
  background-color: $bg-style-primary
}

.brand-bg-4 {
  background-color: $bg-style-secondary;
}

/*2*/
::selection {
  color: #000;
  background: $secondary-color;
}

p {
  margin: 0 0 8px;
}

a {
  color: $secondary-color;

  &:hover {
	color: $primary-color;
  }
}

i,
em {
  font-weight: 400;
}

img {
  max-width: 100%;
  display: block;
}

/*3*/

ol,
ul {
  padding: 0;
  margin: 0;
  counter-reset: li;

  li {
	list-style: none;
	list-style-position: outside;
	position: relative;
	padding-left: 30px;

	&:before {
	  content: counter(li, decimal) ".";
	  counter-increment: li;
	  margin-left: 0;
	  margin-right:0;
	  font-weight: 700;
	  color: $color;
	  position: absolute;
	  top: 0;
	  left: 0
	}
  }
}

/*4*/

ul {
  li {
	&::before {
	  content: "";
	  background-color: $secondary-color;
	  border-radius: 50%;
	  width: 10px;
	  min-width: 10px;
	  height: 10px;
	  top: 7px;
	}
  }
}

/*5-6*/

.font-weight-light {
  font-weight: lighter;
}

.font-weight-bold {
  font-weight: bold;
}

.font-weight-Regular {
  font-weight: 400;
}

.font-weight-Medium {
  font-weight: 500;
}

.font-weight-SemiBold {
  font-weight: 600;
}

/*7-8*/
h1,
h2,
h3,
h4,
h5,
h6,
.title_1,
.title_2,
.title_3,
.title_4,
.title_5,
.title_6 {
  line-height: 1.4;
  color: #000;
  font-weight: 600;
  font-family: $font-secondary;
  margin: 0 0 8px;
}

h1,
.title_1 {
  font-size: 50px;
}

h2,
.title_2 {
  font-size: 24px;
}

h3,
.title_3 {
  font-size: 20px;
}

h4,
.title_4 {
  font-size: 18px;
}

h5,
.title_5 {
  font-size: 16px;
}

h6,
.title_6 {
  font-size: 14px;
}


/*9*/

input[type="checkbox"],
input[type="radio"] {
  display: none;

  &+label {
	color: $white;
	cursor: pointer;

	&:hover {
	  color: $secondary-color;

	}

	&:before {
	  content: '';
	  display: inline-block;
	  vertical-align: middle;
	  cursor: pointer;
	  margin-right: 19px;
	  width: 26px;
	  height: 26px;
	  background-color: transparent;
	  border: 1px solid $white;
	  border-radius: 2px;
	}
  }
}



input[type="radio"]+label:before {
  border-radius: 50%;
}
input[type="checkbox"] + label:before {
  background-color: $white;
}

input[type=radio]:checked+label:before {
  border-radius: 50%;
  background: url(assets/img/radio.svg) center/14px no-repeat transparent ;
  border-color: $white;
}


input:checked+label {
  &:before {
	content: '';
	background: url(assets/img/checkbox.svg) center/12px no-repeat $white;
	border-color: $white;
  }
}


/*10-11*/



.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;

  min-width: 235px;

  padding: 17px 35.5px;

  font-weight: 600;
  font-size: 18px;
  font-family: $font-primary;
  text-decoration: none;

  border: 2px solid $secondary-color;
  line-height: 1.64;

  border-radius: 0;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.btn-primary {
  color: $white;
  background: $secondary-color;

  &:hover {
	color: $secondary-color;
	background-color: transparent;
	border-color: $secondary-color;
  }
}

.btn-secondary {
  color: #2E2E2E;
  background-color: $white;
  border-color: $white;

  &:hover {
	background-color: $bg-style-secondary;
	border-color: $bg-style-secondary;
  }
}

.btn-success {
  color: $primary-color;
  background-color: transparent;
  border: 2px solid transparent;
  border-color: $primary-color;

  &:hover {
	background-color: $secondary-color;
	border-color: $secondary-color;
  }
}

.btn-danger {
  color: #fff;
  background-color: transparent;
  border: 2px solid transparent;
  border-color: #fff;

  &:hover {
	color: #0c0c0c;
	background-color: #fff;
	border-color: #fff;
  }
}

.btn-link {
  color: $primary-color;
  background-color: transparent;

  &:hover {
	color: $primary-color;
  }
}

/*12*/
/*13*/
.scroll-x {
  overflow: auto;
}

.scroll-x::-webkit-scrollbar-thumb {
  background-color: $primary-color;
  border-radius: 5px;
}

.scroll-x::-webkit-scrollbar {
  height: 3px;
  background-color: #DFDCDC;
  border: 0px solid #DFDCDC;
  border-radius: 5px;
}

.scroll::-webkit-scrollbar {
  width: 3px;
  border-radius: 5px;
}

.scroll::-webkit-scrollbar-thumb {
  background-color: $primary-color;
  background-clip: padding-box;
  border: 0px solid #DFDCDC;
  border-radius: 5px;

  &:hover {
	background-color: $secondary-color;
	cursor: pointer;
  }
}

.scroll::-webkit-scrollbar-track {
  border-radius: 5px;
  background-color: #DFDCDC;
  cursor: pointer;
}

::-webkit-scrollbar-button {
  background-color: transparent;
  display: none;
}

/* IE */
.scroll {
  scrollbar-face-color: $primary-color;
  scrollbar-shadow-color: #DFDCDC;
  scrollbar-highlight-color: #DFDCDC;
}

/* FireFox */
.scroll {
  scrollbar-color: $primary-color #DFDCDC;
  padding-right: 15px
}

.scroll {
  overflow-y: scroll;
  overflow-x: hidden;
}


.scroll-heidden::-webkit-scrollbar {
  width: 0px;
}

