/*17*/
/*18*/
.breadcrumb {
  font-size: 14px;
  color: $breadcrumbs-last-child;
  font-weight: 500;
  a {
	text-decoration: none;
	color: $breadcrumbs-link;

	&:hover {
	  color: $breadcrumbs-link-hover;
	}
  }
}

.markup-link {
  position: fixed;
  bottom: 15px;
  left: 15px;
  color: white;
  padding: 5px 10px;
  background: black;
  text-decoration: none;
  border-radius: 50%;

  &:hover {
	color: #fec22d;
  }
}

.slick-arrow {
  position: absolute;
  background-color: rgba(33, 33, 33, 0.9);
  border: 2px solid #C40401;
  border-radius: 3px;
  transform: rotate(45deg);
  transition: background-color ease-in-out 0.3s;
  width: 40px;
  height: 40px;
  top: 40%;
  z-index: 10;
  cursor: pointer;

  @include media('>=tablet') {
	width: 60px;
	height: 60px;
  }

  &__next {
	right: 15px;
  }

  &__prev {
	left: 15px;
  }

  &:after {
	display: none;
  }

  &:hover {
	background-color: #BF0F0D;
  }
}

.slick-dots {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  list-style: none;
  z-index: 10;
  display: flex;
  justify-content: center;

  li {
	padding: 0 17px;
	margin: 0;

	&.slick-active {
	  button {
		 background-color: #C40401;
	   }
	}

	&:before {
	  display: none;
	}
  }

  button {
	background: #878787;
	cursor: pointer;
	border: none;
	width: 12px;
	height: 12px;
	border-radius: 50%;
	line-height: 0;
	font-size: 0;
	transition: background-color ease-in-out 0.3s;

	&:hover {
	  background-color: #C40401;
	}
  }
}

.container {
  width: 100%;
  padding: 0 20px;
  max-width: 1540px;
  margin: 0 auto;

  &_sm {
	max-width: 1340px;
  }

  &_lg {
	max-width: 1640px;
  }
}

.section {
  padding: 80px 0;

  @include media('>=tablet') {
	padding: 130px 0;
  }

  &_sm {
	padding: 40px 0 80px;

	@include media('>=tablet') {
	  padding: 21px 0 130px;
	}
  }
}

.btn-holder {
  text-align: center;
}

.breadcrumbs {
  padding: 20px 0 0;
  background-color: #F5F5F5;

  @include media('>=tablet') {
	padding: 80px 0 0;
  }

  &__list {
	display: flex;
	margin: 0;
	padding: 0;

	li {
	  padding-left: 0;
	  padding-right: 26px;
	  font-weight: 500;
	  font-size: 14px;
	  line-height: 24px;

	  a {
		text-decoration-line: underline;
		color: #121212;
	  }

	  span {
		color: #838383;
	  }

	  &:last-of-type {
		&:after {
		  display: none;
		}
	  }

	  &:before {
		display: none;
	  }

	  &:after {
		position: absolute;
		content: '';
		background-color: #838383;
		transform: rotate(-45deg);
		width: 4px;
		height: 4px;
		top: 45%;
		right: 9px;
	  }
	}
  }
}
