/*home*/
.visual {

  &__slide {
	text-align: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%;
	min-height: 430px;
	display: flex !important;
	justify-content: center;
	align-items: center;

	@include media('>=tablet') {
	  min-height: 640px;
	}
  }

  &__text-holder {
	position: relative;
	text-align: center;
	color: #fff;
	padding: 20px;

	&:before {
	  @include media('>=tablet') {
		content: '';
		position: absolute;
		background-color: #0B0B0C;
		opacity: 0.6;
		top: -30px;
		left: -100px;
		right: -100px;
		bottom: -30px;
		z-index: 0;
		transform: skew(-30deg, 0);
	  }
	}

	& * {
	  position: relative;
	  z-index: 1;
	}
  }

  &__slide-text {
	display: block;
	font-style: italic;
	font-size: 34px;
	line-height: 46px;
	color: #fff;
	margin-bottom: 96px;

	@include media('>=tablet') {
	  font-size: 60px;
	  line-height: 73px;
	  margin-bottom: 14px;
	}
  }

  &__description {
	background-color: #0E181C;
	background-position: 50% 50%;
	background-repeat: no-repeat;
	background-size: cover;
	padding: 80px 0 50px;

	.text-holder {
	  font-size: 16px;
	  line-height: 170%;
	  color: #FFF;

	  p {
		margin-bottom: 30px;
	  }
	}

	h1 {
	  font-family: $font-secondary;
	  font-weight: 600;
	  font-size: 30px;
	  line-height: 38px;
	  color: #FFF;
	  margin-bottom: 20px;

	  @include media('>=tablet') {
		margin-bottom: 30px;
		font-size: 50px;
		line-height: 64px;
	  }
	}
  }

  &__col {
	@include media('>=tablet') {
	  width: 50%;
	  padding: 0 20px;
	}

	@include media('>=1440px') {
	  padding: 0 60px;
	}
  }

  .row {
	@include media('>=tablet') {
	  display: flex;
	  flex-wrap: wrap;
	  align-items: center;
	  margin: 0 -20px;
	}

	@include media('>=1440px') {
	  margin: 0 -60px;
	}
  }
}

.helena {
  background-color: #F7F6F8;

  @include media('>=tablet') {
	padding-bottom: 60px;
  }

  &__img-holder {
	margin-bottom: 40px;

	@include media('>=tablet') {
	  width: 38%;
	}

	img {
	  width: 100%;
	}
  }

  &__description {
	@include media('>=tablet') {
	  width: 62%;
	  padding-left: 100px;
	}

	.text-holder {
	  font-size: 20px;
	  line-height: 170%;
	  color: #000;

	  p {
		margin: 0 0 40px;
	  }
	}

	h2 {
	  font-weight: 600;
	  font-size: 70px;
	  line-height: 85px;
	  color: #ECECED;
	  text-transform: uppercase;
	  font-family: $font-primary;
	  margin-bottom: 40px;

	  @include media('>=tablet') {
		font-size: 100px;
		line-height: 120px;
		margin-bottom: 30px;
	  }

	  @include media('>=1440px') {
		font-size: 130px;
		line-height: 158px;
	  }
	}
  }

  &__sign-img {
	margin-bottom: 40px;
  }

  &__popup-call-holder {
	background-color: $secondary-color;
	color: #fff;
	font-size: 24px;
	line-height: 170%;
	padding: 50px 20px;
	display: flex;
	flex-direction: column;
	align-items: center;

	@include media('>=tablet') {
	  position: relative;
	  font-size: 32px;
	  line-height: 150%;
	  flex-direction: row;
	  padding: 56px 60px;
	  margin-left: 75px;
	  margin-top: -188px;
	  opacity: 0.9;
	  filter: drop-shadow(8px 3px 40px rgba(209, 209, 209, 0.15));
	}

	.text-holder {
	  margin-bottom: 50px;

	  @include media('>=tablet') {
