.title {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-duration: var(--animate-duration);
  animation-duration: var(--animate-duration);
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
  position: relative;
  display: inline-block;
  font-size: 30px;
  line-height: 38px;
  margin: 0 0 30px;
  padding: 0 35px;

  @include media('>=tablet') {
	font-size: 50px;
	line-height: 64px;
	margin: 0 0 23px;
  }

  &:before,
  &:after {
	position: absolute;
	content: '';
	background: #BF0F0D;
	transform: rotate(-45deg);
	width: 10px;
	height: 10px;
	top: 45%;
  }

  &:before {
	left: 0;
  }

  &:after {
	right: 0;
  }
}

.pseudo-title {
  display: block;
  margin: 0 0 30px;
  font-weight: 600;
  font-size: 20px;
  line-height: 172%;
}

.ladies  {
  overflow: hidden;
  background-repeat-y: no-repeat;
  background-size: contain;
  background-position: 0 100%;

  @include media('>=tablet') {
	padding-bottom: 336px;
  }

  &__slider-holder {
	position: relative;
	padding: 128px 0 39px;
  }

  &__visual {
	background-color: #1F232B;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	bottom: 0;
	height: 567px;
	background-repeat-y: no-repeat;
	background-size: contain;
	background-position: 0 100%;

	@include media('>=tablet') {
	  left: 50%;
	  transform: translateX(-50%);
	  max-width: 766px;
	  height: 800px;
	}
  }

  &__slide-description {
	display: flex;
	flex-direction: column;
	align-items: center;
	transition: opacity ease-in-out 0.3s;

	&:first-of-type {
	  position: absolute;
	  top: -218px;
	  left: 50%;
	  transform: translateX(-50%);
	  z-index: 2;

	  span {
		margin: 0;
	  }
	}

	&:last-of-type {
	  position: absolute;
	  bottom: -223px;
	  left: 50%;
	  transform: translateX(-50%);
	  z-index: 2;
	}

	strong {
	  font-weight: 600;
	  font-size: 20px;
	  line-height: 40px;
	  color: #FFF;
	}

	span {
	  font-size: 14px;
	  line-height: 34px;
	  color: #FFFFFF;

	  &.ver {
		position: relative;
		display: block;
		padding-left: 32px;
		white-space: nowrap;
		margin-top: 10px;

		&:before {
		  position: absolute;
		  content: '';
		  top: 50%;
		  left: 0;
		  transform: translateY(-50%);
		  width: 22px;
		  height: 22px;
		  border-radius: 50%;
		  background-color: #B09762;
		}

		&:after {
		  position: absolute;
		  content: '';
		  top: 47%;
		  left: 6px;
		  transform: translateY(-50%) rotate(-45deg);
		  border-bottom: 1px solid #fff;
		  border-left: 1px solid #fff;
		  width: 9px;
		  height: 5px;
		}
	  }
	}

	a {
	  font-weight: 600;
	  font-size: 16px;
	  line-height: 172%;
	  text-decoration-line: underline;
	  color: #fff;
	}
  }

  .btn-holder {
	position: relative;
	bottom: -277px;

	.text-holder {
	  p {
		margin-bottom: 60px;
	  }
	}
  }

  .slick-arrow {
	top: 70%;

	&__next {
	  right: -94px;
	}

	&__prev {
	  left: -94px;
	}
  }
}

.wrap {
  width:100%;
  overflow:hidden;

}
.swiper-container {
  width:100%;
  margin:0 auto;
  overflow:visible;
  height:400px;
}

.swiper-slide {
  display:flex;
  justify-content:center;
  align-items:flex-end;
  width:300px;
  transition:none;

  &.swiper-slide-active {
	.ladies__slide-description {
	  opacity: 1;
	}
  }

  .ladies__slide-description {
	opacity: 0;
  }
}

.swiper-slide .cont {
  text-align:center;
  position: relative;
}

.swiper-slide img {
  width:100%;
  display:block;
}
.swiper-slide span {
  display:inline-block;
  margin-top:20px;
}

.changing,
.changed {
  transform:translateX(-100px);
  user-select: none;
}

.changing {
  transition:transform 0.3s;
  pointer-events:none;
}

.changed {
  transition:none;
}

.changing.swiper-slide-active,
.changed.swiper-slide-active {
  transform:translateX(0);
}

.swiper-slide-active ~ .changing,
.swiper-slide-active ~ .changed {
  transform:translateX(100px);
}

.changing .img-wrap,
.changed .img-wrap {
  transform-origin:center center;
}

.changing .img-wrap {
  transition:transform 0.3s;
}

.changed .img-wrap {
  transition:none;
}

.changing.swiper-slide-active .img-wrap,
.changed.swiper-slide-active .img-wrap {
  transform:scale(1.22);

  @include media('>=tablet') {
	transform:scale(1.99);
  }
}

